/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
/* stylelint-disable declaration-bang-space-before,no-duplicate-selectors */
.jc-badge {
  position: relative;
  display: inline-block;
  line-height: 1;
  vertical-align: middle;
}
.jc-badge-count {
  position: absolute;
  transform: translateX(-50%);
  top: -10px;
  height: 20px;
  border-radius: 10px;
  min-width: 20px;
  background: #f5222d;
  color: #fff;
  line-height: 20px;
  text-align: center;
  padding: 0 6px;
  font-size: 10px;
  font-weight: normal;
  white-space: nowrap;
  transform-origin: -10% center;
  box-shadow: 0 0 0 1px #fff;
}
.jc-badge-count a,
.jc-badge-count a:hover {
  color: #fff;
}
.jc-badge-multiple-words {
  padding: 0 8px;
}
.jc-badge-dot {
  position: absolute;
  transform: translateX(-50%);
  transform-origin: 0 center;
  top: -3px;
  height: 6px;
  width: 6px;
  border-radius: 100%;
  background: #f5222d;
  z-index: 10;
  box-shadow: 0 0 0 1px #fff;
}
.jc-badge-status {
  line-height: inherit;
  vertical-align: baseline;
}
.jc-badge-status-dot {
  width: 6px;
  height: 6px;
  display: inline-block;
  border-radius: 50%;
  vertical-align: middle;
  position: relative;
  top: -1px;
}
.jc-badge-status-success {
  background-color: #52c41a;
}
.jc-badge-status-processing {
  background-color: #1890ff;
  position: relative;
}
.jc-badge-status-processing:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid #1890ff;
  content: '';
  animation: jcStatusProcessing 1.2s infinite ease-in-out;
}
.jc-badge-status-default {
  background-color: #d9d9d9;
}
.jc-badge-status-error {
  background-color: #f5222d;
}
.jc-badge-status-warning {
  background-color: #faad14;
}
.jc-badge-status-text {
  color: rgba(0, 0, 0, 0.65);
  font-size: 12px;
  margin-left: 8px;
}
.jc-badge-zoom-appear,
.jc-badge-zoom-enter {
  animation: jcZoomBadgeIn 0.3s cubic-bezier(0.12, 0.4, 0.29, 1.46);
  animation-fill-mode: both;
}
.jc-badge-zoom-leave {
  animation: jcZoomBadgeOut 0.3s cubic-bezier(0.71, -0.46, 0.88, 0.6);
  animation-fill-mode: both;
}
.jc-badge-not-a-wrapper .jc-scroll-number {
  top: auto;
  display: block;
  position: relative;
}
.jc-badge-not-a-wrapper .jc-badge-count {
  transform: none;
}
@keyframes jcStatusProcessing {
  0% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: scale(2.4);
    opacity: 0;
  }
}
.jc-scroll-number {
  overflow: hidden;
}
.jc-scroll-number-only {
  display: inline-block;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  height: 20px;
}
.jc-scroll-number-only > p {
  height: auto;
  margin: 0;
}
@keyframes jcZoomBadgeIn {
  0% {
    opacity: 0;
    transform: scale(0) translateX(-50%);
  }
  100% {
    transform: scale(1) translateX(-50%);
  }
}
@keyframes jcZoomBadgeOut {
  0% {
    transform: scale(1) translateX(-50%);
  }
  100% {
    opacity: 0;
    transform: scale(0) translateX(-50%);
  }
}
