.top-tools-wrap {
  font-size: 12px;
  height: 30px;
  background-color: #333;
}
.top-tools-wrap .top-tools-container {
  margin: 0 auto;
  width: 1200px;
  line-height: 30px;
}
.top-tools-wrap .top-tools-container .down-menu {
  padding: 0 10px;
  position: relative;
  height: 30px;
}
.top-tools-wrap .top-tools-container .down-menu > .down-menu-cont {
  display: none;
}
.top-tools-wrap .top-tools-container .down-menu > a {
  color: #eee;
}
.top-tools-wrap .top-tools-container .down-menu:hover {
  background: #f7f7f7;
  border-bottom: 1px solid #e5e5e5;
}
.top-tools-wrap .top-tools-container .down-menu:hover > .down-menu-cont {
  background: #f7f7f7;
  display: block;
  position: absolute;
  z-index: 9;
}
.top-tools-wrap .top-tools-container .down-menu:hover > a {
  color: #333;
}
.top-tools-wrap .localCont {
  padding: 5px;
  width: 400px;
  left: 0;
}
.top-tools-wrap .localCont li {
  float: left;
  margin: 1px 3px;
}
.top-tools-wrap .localCont li span {
  cursor: pointer;
  padding: 5px 10px;
}
.top-tools-wrap .localCont li .active,
.top-tools-wrap .localCont li span:hover {
  background: #e43636;
  color: #eee;
}
.top-tools-wrap .user {
  width: 200px;
  padding: 10px 20px;
  line-height: 40px;
  left: 0;
}
.top-tools-wrap .user .user-pic * {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  float: left;
  margin-right: 10px;
}
.top-tools-wrap .user .user-pic p {
  background: #ccc;
}
.top-tools-wrap .user .user-pic p * {
  font-size: 24px;
  margin-top: 7px;
  color: #f8f8f8;
}
.top-tools-wrap .user a {
  padding: 0 10px;
}
.top-tools-wrap .select {
  padding: 10px;
  height: 130px;
  overflow-y: auto;
  left: 0;
}
.top-tools-wrap .select li {
  height: 26px;
  line-height: 26px;
  overflow: hidden;
}
.top-tools-wrap .select li a {
  display: block;
  padding: 0 10px;
  color: #999;
}
.top-tools-wrap .select li a:hover,
.top-tools-wrap .select li .active {
  background: #eee;
}
.top-tools-wrap .my-mall {
  width: 300px;
  padding: 10px 20px;
  left: 0;
}
.top-tools-wrap .my-mall li {
  width: 33.3333%;
  float: left;
}
.top-tools-wrap .my-mall li dl dd {
  height: 22px;
  line-height: 22px;
}
.top-tools-wrap .codeImg {
  width: 200px;
  right: 0 ;
  padding: 0 10px;
}
.top-tools-wrap .codeImg li {
  padding: 10px;
}
.top-tools-wrap .codeImg li img {
  width: 85px;
  height: 85px;
  float: left;
  margin-right: 10px;
  background: #eee;
}
.top-tools-wrap .codeImg li + li {
  border-top: 1px dashed #ccc;
}
.top-tools-wrap .width380 {
  width: 380px;
}
.top-tools-wrap .signName {
  color: #eee;
  padding: 0 15px;
}
.top-tools-wrap .mt2 {
  margin-top: 2px;
}
.top-tools-wrap .active-text {
  color: #e43636;
}
.top-tools-wrap .vertical {
  height: 11px;
  width: 1px;
  background: transparent;
  display: inline-block;
  position: relative;
  top: 1px;
}
.top-tools-wrap .bg-eee {
  background-color: #eee;
}
.top-tools-wrap .bg-999 {
  background-color: #999;
}
.top-tools-wrap .bg-666 {
  background-color: #666;
}
.top-tools-wrap .deg180:hover .arrows {
  transform: rotate(180deg);
}
.top-tools-wrap .phone-icon {
  font-size: 20px;
  position: relative;
  top: 3px;
}
.top-tools-wrap .badge {
  position: absolute;
  padding: 0 4px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  border-radius: 10px;
  z-index: 1;
  background-color: #ff4728;
  color: #fff;
  left: 25px;
  line-height: 14px;
  margin-top: 2px;
}
.unChooseLocal ul:after {
  clear: both;
  display: block;
  content: ' ';
}
.unChooseLocal ul li {
  float: left;
  width: 25%;
  text-align: center;
}
.unChooseLocal ul li span {
  cursor: pointer;
  padding: 5px;
  margin: 0 5px;
  display: block;
}
.unChooseLocal ul li span:hover {
  background: #e43636;
  color: #fff;
}
